import React from 'react'
import { Overflow, MainAxisSize } from '../../style'
import { Container } from '../..'

export type RowProps = {
  className?: string
  mainAxisSize?: MainAxisSize
  overflow?: Overflow
}

/**
 * 上下布局的组件。
 * 高度是最高的子组件的高度
 */
export default function Row({ mainAxisSize = 'max', ...restProps }: React.PropsWithChildren<RowProps>) {
  return <Container debuggerLabel="Row" direction="row" width={decodeMainAxisSize(mainAxisSize)} {...restProps} />
}


export function decodeMainAxisSize(size: MainAxisSize) {
  if (size === 'min') {
    return 'auto'
  }

  return '100%'
}
