import React from 'react'
import { Overflow, MainAxisSize } from '../../style'
import Container from '../Container'

export type ColumnProps = {
  className?: string
  mainAxisSize?: MainAxisSize
  overflow?: Overflow
}

/**
 * 上下布局的组件。
 * 高宽默认占满父容器。
 * 默认占满父容器
 */
export default function Column({ mainAxisSize = 'max', ...restProps }: React.PropsWithChildren<ColumnProps>) {
  return <Container debuggerLabel="Column" direction="column" width="100%" height={decodeMainAxisSize(mainAxisSize)} {...restProps} />
}

export function decodeMainAxisSize(size: MainAxisSize) {
  if (size === 'min') {
    return 'auto'
  }

  return '100%'
}
